<template>
    <div class="ma-3">
        <v-container class="container" v-if="listQuizz">
           <v-card
            class="mx-auto"
            max-width="70vw"
           >
                <!-- Question  -->
                <v-card-title>
                    <span>{{ currentQuestion }}</span>
                    <v-progress-linear
                        v-model="quizz"
                        color="teal darken-2"
                    ></v-progress-linear>
                </v-card-title>

                <!-- Options  -->
                <v-window v-model="step">
                    <v-window-item
                        :value="1"
                    >
                        <SData :stp_data="listQuizz[0]" :stp_n_step="2"/>
                    </v-window-item>
                    <v-window-item
                        :value="2"
                    >
                        <SData :stp_data="listQuizz[1]" :stp_n_step="3"/>
                    </v-window-item>
                </v-window>
           </v-card>
        </v-container>
        <Skeleton v-else/>
    </div>
</template>

<script>
import SData from './Quizz/Quizz_Step_Data';
import Skeleton from './Quizz/Quizz_Skeleton';

export default {
    name: 'Quizz',
    components: { Skeleton, SData },
    data() {
        return {
            listQuizz: Array,
            quizz: 0,
            step: 1,
        }
    },
    mounted() {
        fetch('http://localhost:3000/quizz/list')
            .then(  res     =>  res.json())
            .then(  data    =>  {
                this.listQuizz = data;
                this.quizz = Math.trunc(100/data.length);
                console.log(this.quizz)
                console.log({data})
            })
            .catch( err     => console.error({err}))
    },
    computed: {
        currentQuestion() {
            if(this.listQuizz.length > 0) {
                return this.listQuizz[this.step-1].questions;
            }

            return 'Loading from Server.'
        }
    }
}
</script>

